import Link from 'next/link';
import { 
  TrendingUp, 
  Landmark, 
  Calculator, 
  Coins, 
  Search, 
  ArrowRight,
  Percent,
  PieChart,
  ShieldCheck,
  Zap
} from 'lucide-react';

export default function Home() {
  const quickLinks = [
    { name: 'SIP Calculator', href: '/sip-calculator', color: 'text-emerald-600 bg-emerald-50 border-emerald-200 hover:bg-emerald-100' },
    { name: 'Home Loan EMI', href: '/emi-calculator', color: 'text-rose-600 bg-rose-50 border-rose-200 hover:bg-rose-100' },
    { name: 'GST Calc', href: '#', color: 'text-purple-600 bg-purple-50 border-purple-200 hover:bg-purple-100' },
    { name: 'Income Tax', href: '#', color: 'text-blue-600 bg-blue-50 border-blue-200 hover:bg-blue-100' }
  ];

  return (
    <div className="min-h-screen bg-slate-50 font-sans selection:bg-indigo-100">
      {/* Navbar */}
      <nav className="bg-white/90 backdrop-blur-xl border-b border-slate-200 sticky top-0 z-50 transition-all">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="flex justify-between h-16 items-center">
            <div className="flex items-center gap-2 group cursor-pointer">
              <div className="bg-gradient-to-tr from-blue-600 via-indigo-600 to-violet-600 p-2.5 rounded-xl shadow-lg shadow-blue-500/20 group-hover:scale-110 transition-transform duration-300">
                <Calculator className="h-5 w-5 text-white" />
              </div>
              <span className="text-xl font-black text-slate-900 tracking-tight">
                FinCal<span className="text-transparent bg-clip-text bg-gradient-to-r from-blue-600 to-violet-600">Hub</span>
              </span>
            </div>
            <div className="hidden md:flex gap-8 text-sm font-bold text-slate-600">
              <Link href="#" className="hover:text-indigo-600 transition-colors">Calculators</Link>
              <Link href="#" className="hover:text-indigo-600 transition-colors">Resources</Link>
              <div className="h-6 w-px bg-slate-200"></div>
              <Link href="#" className="hover:text-indigo-600 transition-colors">Login</Link>
            </div>
          </div>
        </div>
      </nav>

      {/* Colorful Hero Section */}
      <div className="relative overflow-hidden bg-white">
        {/* Gradient Blobs */}
        <div className="absolute top-0 left-0 w-full h-full overflow-hidden pointer-events-none opacity-40">
            <div className="absolute -top-[20%] -left-[10%] w-[70%] h-[70%] bg-purple-300 rounded-full mix-blend-multiply filter blur-[128px] animate-blob"></div>
            <div className="absolute top-[0%] right-[0%] w-[60%] h-[60%] bg-cyan-300 rounded-full mix-blend-multiply filter blur-[128px] animate-blob animation-delay-2000"></div>
            <div className="absolute -bottom-[20%] left-[20%] w-[60%] h-[60%] bg-pink-300 rounded-full mix-blend-multiply filter blur-[128px] animate-blob animation-delay-4000"></div>
        </div>

        <div className="relative max-w-5xl mx-auto px-4 py-24 sm:px-6 lg:px-8 text-center z-10">
          <div className="inline-flex items-center gap-2 px-4 py-2 rounded-full bg-white/80 border border-indigo-100 text-indigo-700 text-sm font-bold mb-8 shadow-sm backdrop-blur-sm">
            <Zap className="w-4 h-4 fill-yellow-400 text-yellow-500" /> India's Fastest Finance Tools
          </div>
          
          <h1 className="text-5xl sm:text-7xl font-black text-slate-900 mb-8 tracking-tight leading-[1.1]">
            Make Smarter <br className="hidden sm:block" />
            <span className="text-transparent bg-clip-text bg-gradient-to-r from-indigo-600 via-purple-600 to-pink-600">
              Money Moves.
            </span>
          </h1>
          
          <p className="text-xl text-slate-600 mb-12 max-w-2xl mx-auto leading-relaxed font-medium">
            Calculate your future. Whether it's building wealth with SIPs or planning a loan, get accurate results in seconds.
          </p>

          {/* Search Bar */}
          <div className="max-w-xl mx-auto relative mb-12 group">
            <div className="absolute -inset-1 bg-gradient-to-r from-blue-600 via-purple-600 to-pink-600 rounded-2xl blur opacity-25 group-hover:opacity-50 transition duration-500"></div>
            <div className="relative bg-white rounded-2xl shadow-2xl shadow-slate-200/50 flex items-center p-2 border border-slate-100">
              <div className="pl-4 text-slate-400">
                <Search className="h-6 w-6" />
              </div>
              <input
                type="text"
                className="w-full px-5 py-4 bg-transparent text-slate-900 placeholder-slate-400 focus:outline-none text-lg font-medium"
                placeholder="Try 'Home Loan' or 'SIP'..."
              />
              <button className="bg-slate-900 text-white px-8 py-3 rounded-xl font-bold hover:bg-slate-800 transition-all hover:scale-105 active:scale-95">
                Go
              </button>
            </div>
          </div>

          {/* Colorful Pills */}
          <div className="flex flex-wrap justify-center gap-3">
            {quickLinks.map((item) => (
              <Link 
                key={item.name} 
                href={item.href}
                className={`px-5 py-2.5 rounded-full text-sm font-bold transition-all duration-200 hover:-translate-y-1 hover:shadow-lg ${item.color} border`}
              >
                {item.name}
              </Link>
            ))}
          </div>
        </div>
      </div>

      {/* Main Content Grid */}
      <main className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-20">
        
        {/* Category 1: Investments (Green Theme) */}
        <div className="mb-24">
          <div className="flex items-center gap-4 mb-10">
            <div className="p-3 bg-emerald-100 rounded-2xl text-emerald-600">
              <TrendingUp className="h-8 w-8" />
            </div>
            <div>
                <h2 className="text-3xl font-black text-slate-900">Grow Your Wealth</h2>
                <p className="text-slate-500 font-medium mt-1">Tools to help your money make money.</p>
            </div>
          </div>
          
          <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-4 gap-6">
            {/* SIP Calculator Card - ACTIVE */}
            <Link href="/sip-calculator" className="group relative bg-white rounded-3xl p-1 shadow-xl shadow-emerald-100 hover:shadow-2xl hover:shadow-emerald-200 transition-all duration-300 hover:-translate-y-2">
              <div className="absolute inset-0 bg-gradient-to-br from-emerald-400 to-teal-500 rounded-3xl opacity-0 group-hover:opacity-10 transition-opacity"></div>
              <div className="relative bg-white rounded-[22px] p-7 h-full border border-slate-100 group-hover:border-emerald-100 transition-colors">
                <div className="flex justify-between items-start mb-8">
                    <div className="h-14 w-14 bg-emerald-50 rounded-2xl flex items-center justify-center group-hover:bg-emerald-500 transition-colors duration-300">
                        <TrendingUp className="h-7 w-7 text-emerald-600 group-hover:text-white transition-colors" />
                    </div>
                    <div className="bg-emerald-50 px-2 py-1 rounded-lg">
                        <ArrowRight className="h-5 w-5 text-emerald-600 -rotate-45 group-hover:rotate-0 transition-transform duration-300" />
                    </div>
                </div>
                <h3 className="text-xl font-bold text-slate-900 mb-3">SIP Calculator</h3>
                <p className="text-slate-500 mb-6 leading-relaxed font-medium">Check how â‚¹5,000/mo turns into Crores over 20 years.</p>
                <span className="text-sm font-bold text-emerald-600 group-hover:underline underline-offset-4 decoration-2">Calculate Returns</span>
              </div>
            </Link>

            {/* Placeholders */}
            {[
              { icon: PieChart, title: 'Lumpsum Calc', desc: 'One-time investment returns.' },
              { icon: Percent, title: 'FD Calculator', desc: 'Fixed deposit interest rates.' },
              { icon: Landmark, title: 'PPF Calculator', desc: 'Tax-free government savings.' }
            ].map((card) => (
              <div key={card.title} className="bg-slate-50/50 rounded-3xl p-8 border-2 border-dashed border-slate-200 opacity-70 hover:opacity-100 hover:bg-white hover:border-emerald-200 transition-all cursor-pointer">
                <div className="h-12 w-12 bg-white rounded-xl flex items-center justify-center mb-6 shadow-sm border border-slate-100">
                  <card.icon className="h-6 w-6 text-slate-400" />
                </div>
                <h3 className="text-lg font-bold text-slate-900 mb-2">{card.title}</h3>
                <p className="text-sm text-slate-500 font-medium">{card.desc}</p>
              </div>
            ))}
          </div>
        </div>

        {/* Category 2: Loans (Orange/Rose Theme) */}
        <div className="mb-16">
          <div className="flex items-center gap-4 mb-10">
            <div className="p-3 bg-rose-100 rounded-2xl text-rose-600">
              <Landmark className="h-8 w-8" />
            </div>
            <div>
                <h2 className="text-3xl font-black text-slate-900">Manage Debt</h2>
                <p className="text-slate-500 font-medium mt-1">Smart planning for loans and mortgages.</p>
            </div>
          </div>
          
          <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-6">
            {/* Home Loan - ACTIVE */}
            <Link href="/emi-calculator" className="group relative bg-white rounded-3xl p-1 shadow-xl shadow-rose-100 hover:shadow-2xl hover:shadow-rose-200 transition-all duration-300 hover:-translate-y-2">
              <div className="absolute inset-0 bg-gradient-to-br from-rose-400 to-orange-500 rounded-3xl opacity-0 group-hover:opacity-10 transition-opacity"></div>
               <div className="relative bg-white rounded-[22px] p-7 h-full border border-slate-100 group-hover:border-rose-100 transition-colors">
                <div className="flex justify-between items-start mb-8">
                    <div className="h-14 w-14 bg-rose-50 rounded-2xl flex items-center justify-center group-hover:bg-rose-500 transition-colors duration-300">
                        <Landmark className="h-7 w-7 text-rose-600 group-hover:text-white transition-colors" />
                    </div>
                     <div className="bg-rose-50 px-2 py-1 rounded-lg">
                        <ArrowRight className="h-5 w-5 text-rose-600 -rotate-45 group-hover:rotate-0 transition-transform duration-300" />
                    </div>
                </div>
                <h3 className="text-xl font-bold text-slate-900 mb-3">Home Loan EMI</h3>
                <p className="text-slate-500 mb-6 leading-relaxed font-medium">Know exactly what you pay before you borrow.</p>
                <span className="text-sm font-bold text-rose-600 group-hover:underline underline-offset-4 decoration-2">Check EMI</span>
              </div>
            </Link>

            {/* Placeholders */}
            {[
              { icon: Coins, title: 'Personal Loan', desc: 'Quick loan EMI planning.' },
              { icon: Calculator, title: 'Loan Eligibility', desc: 'How much can you borrow?' }
            ].map((card) => (
               <div key={card.title} className="bg-slate-50/50 rounded-3xl p-8 border-2 border-dashed border-slate-200 opacity-70 hover:opacity-100 hover:bg-white hover:border-rose-200 transition-all cursor-pointer">
                <div className="h-12 w-12 bg-white rounded-xl flex items-center justify-center mb-6 shadow-sm border border-slate-100">
                  <card.icon className="h-6 w-6 text-slate-400" />
                </div>
                <h3 className="text-lg font-bold text-slate-900 mb-2">{card.title}</h3>
                <p className="text-sm text-slate-500 font-medium">{card.desc}</p>
              </div>
            ))}
          </div>
        </div>

      </main>
      
      {/* Dark Footer */}
      <footer className="bg-slate-950 text-slate-400 py-20">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 grid grid-cols-1 md:grid-cols-4 gap-12">
          <div className="col-span-1 md:col-span-2">
             <span className="text-2xl font-black text-white tracking-tight">
                  FinCal<span className="text-blue-500">Hub</span>
                </span>
              <p className="mt-6 text-slate-400 text-sm max-w-xs leading-relaxed font-medium">
                Built for the modern Indian investor. Simple, fast, and beautifully designed financial tools.
              </p>
          </div>
          <div>
            <h4 className="text-sm font-bold uppercase tracking-wider text-white mb-6">Calculators</h4>
            <ul className="space-y-4 font-medium">
              <li className="hover:text-white cursor-pointer transition-colors">SIP Calculator</li>
              <li className="hover:text-white cursor-pointer transition-colors">Home Loan EMI</li>
              <li className="hover:text-white cursor-pointer transition-colors">Income Tax</li>
            </ul>
          </div>
          <div>
            <h4 className="text-sm font-bold uppercase tracking-wider text-white mb-6">Legal</h4>
            <ul className="space-y-4 font-medium">
              <li className="hover:text-white cursor-pointer transition-colors">Privacy</li>
              <li className="hover:text-white cursor-pointer transition-colors">Terms</li>
              <li className="hover:text-white cursor-pointer transition-colors">Contact</li>
            </ul>
          </div>
        </div>
      </footer>
    </div>
  );
}